<template>
  <div class="calculator-container">
    <div class="display">{{ current || 0 }}</div>
    <div class="keys">
      <button @click="operator('clear')" class="btn operator">AC</button>
      <button @click="operator('Backspace')" class="btn operator">Del</button>
      <button @click="operator('percent')" class="btn operator">%</button>
      <button @click="operator('divide')" class="btn operator">/</button>
      <button @click="number(7)" class="btn">7</button>
      <button @click="number(8)" class="btn">8</button>
      <button @click="number(9)" class="btn">9</button>
      <button @click="operator('multiply')" class="btn operator">*</button>
      <button @click="number(4)" class="btn">4</button>
      <button @click="number(5)" class="btn">5</button>
      <button @click="number(6)" class="btn">6</button>
      <button @click="operator('subtract')" class="btn operator">-</button>
      <button @click="number(1)" class="btn">1</button>
      <button @click="number(2)" class="btn">2</button>
      <button @click="number(3)" class="btn">3</button>
      <button @click="operator('add')" class="btn operator">+</button>
      <button @click="number(0)" class="btn zero">0</button>
      <button @click="operator('dot')" class="btn">.</button>
      <button @click="operator('eval')" class="btn operator">=</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Calculator',
  data() {
    return {
      current: "",
      isAnswered: false,
      operatorClicked: false,
    }
  },
  methods: {
    operatorIsClicked() {
      this.operatorClicked = true;
      this.isAnswered = false;
    },
    operator(arg) {
      switch (arg) {
        case "clear":
          this.current = "";
          this.operatorClicked = false;
          break;
        case "Backspace":
          if (this.current) {
            this.current = this.current.length > 1 ? this.current.slice(0, this.current.length - 1) : ""
            this.operatorClicked = false;
          }
          break;
        case "percent":
          this.current = parseFloat(this.current) / 100;
          this.operatorClicked = false;
          break;
        case "divide":
          this.current = (!this.operatorClicked && this.current) ? `${this.current}/` : this.current;
          this.operatorIsClicked();
          break;
        case "multiply":
          this.current = (!this.operatorClicked  && this.current) ? `${this.current}*` : this.current;
          this.operatorIsClicked();
          break;
        case "add":
          this.current = (!this.operatorClicked  && this.current) ? `${this.current}+` : this.current;
          this.operatorIsClicked();
          break
        case  "subtract":
          this.current = (!this.operatorClicked  && this.current) ? `${this.current}-` : this.current;
          this.operatorIsClicked();
          break;
        case "eval":
          this.current = this.current? `${ eval(this.current).toLocaleString()}`: "";
          this.isAnswered = true;
          break
        case "dot":
          if (this.current.indexOf(".") === -1) {
            //Start with zero if the current status is empty
            let i = !this.current? "0.":".";
            this.number(i);
          }
          break;
        default:
          console.log(arg)
          break;
      }
    },
    number(arg) {
      //Clear the digits if the previous calculation is still true
      if (this.isAnswered) {
        this.current = ""
        this.isAnswered = false
      }
      if (this.operatorClicked) this.operatorClicked = false;
      //Prevent the current from starting from zero
      if (!this.current && arg === 0) return;
      this.current = this.current !== null  ? `${this.current}${arg}` : "";
    }
  }
}
</script>

<style scoped>
.calculator-container {
  max-width: 400px;
  margin: auto;
  box-shadow: 3px 9px 13px #0000000d;
}
.keys {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(50px, 1fr);

}

.calculator-container .display {
  background-color: #171616;
  color: #fff;
  padding: 4rem 0.8em 0.8rem;
  text-align: right;
  font-size: 1.5em;
  overflow: hidden;
  word-break: break-word;
  max-height: 70px;
  overflow-y: auto;
  letter-spacing: 1px;
  border-radius: 4px 4px 0 0;
}

.calculator-container .display::-webkit-scrollbar {
  background-color: #313030;
  height: 5px;
}

.calculator-container .display::-webkit-scrollbar-thumb {
  background-color: #676666;
}

.calculator-container .btn {
  border: 1px solid #c7c5c5;
  cursor: pointer;
}

.calculator-container .zero {
  grid-column: 1 / 3;
}

.operator {
  background-color: #5484da;
  color: #ffffff;
}
</style>
